    .row.justify-content-center.align-items-center.mb-4(data-aos="flip-rigth")
      p Entre las buenas prácticas de comunicación de novedades se destacan:
      p Uso de canales diversos: combinar medios verbales (anuncios, reuniones), escritos (bitácoras, comunicados, carteleras informativas) y digitales (mensajería instantánea, redes sociales, correo electrónico). Esta diversidad garantiza que la información llegue oportunamente a todos los actores involucrados, respetando sus contextos y preferencias comunicativas.
      p Lenguaje inclusivo y culturalmente pertinente: adaptar el mensaje al contexto local, evitando tecnicismos y promoviendo el respeto por la diversidad lingüística y cultural. En territorios con lenguas nativas, se recomienda realizar traducciones o apoyarse en mediadores culturales que garanticen la comprensión mutua.
      p Actualización permanente: informar con anticipación sobre cualquier cambio o novedad relevante, evitando la improvisación, la confusión o la desinformación. Una comunicación oportuna refuerza la confianza entre visitantes, comunidades y operadores.

    .row.justify-content-center.align-items-center.mb-4(data-aos="flip-left")
      p Retroalimentación continua: generar espacios de diálogo participativo con los usuarios y actores locales para recoger percepciones, aclarar dudas y ajustar las estrategias comunicativas. Este intercambio permite fortalecer la transparencia y la corresponsabilidad en la gestión turística.
      p Registro documental: dejar evidencia escrita o digital de los comunicados, acuerdos o decisiones tomadas, lo que contribuye a la trazabilidad, la memoria institucional y la rendición de cuentas.
